import { Heading, Spacer, Stack } from "@chakra-ui/react";
import React, { lazy, FC, MouseEventHandler, ReactNode } from "react";

interface DashboardCardItemProps {
  icon: ReactNode;
  cardName: string;
  onClick?: MouseEventHandler<HTMLDivElement> | undefined;
}

const DashboardCardItem: FC<DashboardCardItemProps> = ({
  icon,
  cardName,
  onClick,
}): JSX.Element => {
  const { root, text } = useStyles();

  return (
    <Stack
      {...root}
      as="button"
      _hover={{ bg: "dark", color: "white" }}
      onClick={onClick}
    >
      {icon}
      <Spacer />
      <Heading {...text}>{cardName}</Heading>
    </Stack>
  );
};

export default DashboardCardItem;

const useStyles = () => {
  return {
    root: {
      borderRadius: {
        base: "10px",
        md: "16px",
      },
      bg: "grey",
      p: ["10px", "15px", "18px", "20px", "25px"],
      h: "150px",
    },
    text: {
      fontSize: ["12px", "14px", "16px", "16px", "20px"],
    },
  };
};
